.mainContainer {
    width: 100%;
    display: flex;
    justify-content: center;
    font-size: 23px;


}

.addTask{
    width: 545px;
   display: flex;
    justify-content: end;
}
.newTask{
    width: 700px;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color: #f6f6f6;
    margin-top: 10px;
    border-bottom: 1px solid black;
    margin-bottom: 10px;
}
.newTask input{
    width: 100px;
    height: 40px;
    font-size: 20px;
}
.newTask checkbox{
    width: 10px;
    height: 10px;
    font-size: 10px;
}